import React, {useState} from "react";
import styles from './Paginator.module.css';

function Paginator(props) {
    let pagesCount = Math.ceil(props.totalItemsCount / props.pageSize);

    let pages = [];
    for (let i = 1; i <= pagesCount; i++) {
        pages.push(i);
    }

    let portionCount = Math.ceil(pagesCount / props.pageSize);
    let [portionNumber, setPortionNumber] = useState(1);
    let leftPortionPageNumber = (portionNumber - 1) * props.pageSize + 1;
    let rightPortionPageNumber = portionNumber * props.pageSize;

    return (
            <div>
                {portionNumber > 1 &&
                <button onClick={() => {setPortionNumber(portionNumber - 1)}}>left</button>}
                {pages
                    .filter(p => p >= leftPortionPageNumber && p<rightPortionPageNumber)
                    .map(p => {
                    return <span className={props.currentPage === p && styles.selectedPage}
                                 onClick={() => {
                                     props.onPageChanged(p);
                                 }}>{p}</span>
                })}
                {portionCount > portionNumber &&
                <button onClick={() => {setPortionNumber(portionNumber + 1)}}>right</button>}
            </div>
    )
}


export default Paginator;